@mixin largeLaptop {
    @media (max-width: 1160px) {
        @content
    }
}
@mixin laptop {
    @media (max-width: 992px) {
        @content
    }
}


@mixin tablet {
    @media (max-width: 768px) {
        @content
    }
}

@mixin largeMobile {
    @media (max-width: 425px) {
        @content
    }
}

@mixin mediumMobile{
    @media (max-width: 375px){
        @content
    }
}

@mixin smallMobile {
    @media (max-width: 320px) {
        @content
    }
}